body {
  background: #000;

  .wrap {
    width: 100%;
    background: #000;

    .content {
      .inner {
        margin: 40px auto;
        width: 1200px;

        h2 {
          text-align: center;
          align-items: center;
          border-bottom: 4px solid #fdfbf9;
          margin-bottom: 30px;
          img {
            width: 50%;
          }
        }

        .CharacterList {
          display: flex;
          flex-wrap: wrap;
          justify-content: center;

          .CharacterItem {
            border: 1px solid #fdfbf9;
            padding: 20px;
            width: 200px;
            box-sizing: border-box;
            display: flex;
            flex-direction: column;
            align-items: center;
            margin-bottom: 30px;
            margin-right: 30px;
            position: relative;

            strong {
              font-size: 30px;
              font-weight: 700;
              color: #000;
              background: #fdfbf9;
              border-radius: 50%;
              width: 40px;
              height: 40px;
              line-height: 40px;
              display: block;
              position: absolute;
              left: -20px;
              top: -15px;
              text-align: center;
            }

            .CharacterImg {
              img {
                width: 150px;
                border-radius: 50%;
                transition: 0.3s;
                padding-top: 20px;
              }
            }

            &:hover {
              img {
                transform: scale(1.1);
              }
            }

            span {
              font-weight: 700;
              color: #fdfbf9;
              padding-top: 20px;
              font-size: 25px;
            }
          }
        }
      }
    }
  }
}
